@import url(https://fonts.googleapis.com/css2?family=Otomanopee+One&display=swap);
/*FONT STYLE*/


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  scroll-behavior: smooth;
  /* color: rgb(221, 211, 211); */
}

.app {
  min-height: 100vh;
  background-color: #39445a; 
  color: white;
  padding-top: 130px;
  padding-bottom: 70px;
}



.pageTitle {
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  font-family: "Montserrat", sans-serif;
  font-size: 2vw;
  padding: 4px;
  border-radius: 50px;
  color: white;
}

@media (max-width: 1000px) {
  .pageTitle {
    font-size: 6.4vw;
  }
}

body {
  background: #870000;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #190A05, #870000); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}



.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (max-width: 700px) {
  .app {
    padding-top: 70px;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}


.trending {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 30px;
  margin: 2%;
}

.header{
    width:100%;
    cursor:pointer;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    align-items: center;
    display: flex;
    justify-content:space-evenly;
    text-transform: uppercase;
    background: #200122;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #6f0000, #200122); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    padding-left: 4%;
    font-size:3vw;
    box-shadow: 0px 1px 5px black;
    color: rgb(39, 39, 39);
    z-index: 100;
    font-family: 'Open Sans Condensed', sans-serif;
}

.estilos-imagen{
    width: 260px;    
}


.title-Header{
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    color: rgb(181 176 176);
    font-size: x-large;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-weight: 500;
}

@media (max-width:1000px) {
    .header{
        padding: top 15px;
        font-size: 6.4vw;
    }
}
.loading{
    height: 70vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
.slider-movies{
    height: 53vh;
    width: 100%;
}

.slider-movies__movie{
    height: 55vh;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.slider-movies__movie-info{
    display: flex;
    align-items: flex-end;
    height: 100%;
    padding-bottom: 50px;
}
            
.div-info{
    background-color: #8d8c8ccf;
    margin: 0 auto;
    text-align: center;
    width: 60%;
    padding-bottom: 13px;
    color: black;
}

.boton{
    padding-bottom: 5px;
    color:rgb(212, 202, 202);
    border-radius: 70px;
}

.titulo{
    color: rgb(39, 39, 39);
}

.parrafo{
    padding-bottom: 10px;
    color: rgb(36, 36, 36);
}
.movie-list{
    width: 70%;
    margin: 0 auto !important; 
    margin-top: 60px !important; 
    border-color: rgb(65, 120, 122) !important;
}

.ant-list-header{
    background-color: rgb(45 41 51);
}

h2{
    text-align: center;
    margin: 0;
    color: white;
}

.ant-list-items{
    height: 60vh;
    overflow: scroll;
    background-color: rgb(176, 188, 189);
}

.ant-list-item-meta{
    align-items: center;
}

.ant.list.item-meta-title{
    margin: 0
}

.boton-info{
    background-color: rgb(101, 97, 109);
}
.media {
    display: flex;
    flex-direction: column;
    width: 200px;
    padding: 5px;
    margin: 5px 0;
    /* background-color: #282c34; */
    border-radius: 10px;
    position: relative;
    color: white;
    font-family: "Montserrat", sans-serif;
  }

  .media:hover {
    transform: scale(1.1);
    box-shadow: 5px 0px 3px rgba(0, 0, 0, 0.404);
}

  @media (max-width: 550px) {
    .media {
      width: 46%;
    }
  }
  
  .poster {
    border-radius: 10px;
  }
  
  .title {
    width: 100%;
    text-align: center;
    font-size: 17px;
    padding: 8px 0;
  }
  
  .subTitle {
    display: flex;
    justify-content: space-between;
    padding-bottom: 3px;
    padding: 0 66px;
    padding-bottom: 3px;
  }
.paginado{
    width: 100%;
    display: flex;
    justify-content: center;
    margin: top 10px;
    background-color: darkred;
}


.paginationStyles{
    align-items: center;
    margin-top: 7%;
}

.search {
    display: flex;
    margin: 15px 0;
}
.footer{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(45 41 51);
    margin-top: 50px;
    padding: 15px 0;
    bottom:0;
    width:100%;
    position: fixed;

    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(45 41 51);
    margin-top: 50px;
    padding: 0px 0;
    z-index: 100;
 }

.texto{
    margin: 0;
    color: white;
    font-size: 15px;
}

.ant-layout-footer{
    padding: 0px 50px;
}
